import styled from "styled-components";

export const Section = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 800px;
  min-width: 350px;
  background-color: rgba(220, 220, 220);
  padding: 20px 20px;
  border: 2px solid #2e1414;
  border-radius: 10px;
  margin: auto;
  width: 80%;
  text-align: center;
`;